<template>
	<view class="room_bar">
		<chatSuitEmoji ref="chatSuitEmoji" @newEmojiStr="emojiAction"></chatSuitEmoji>
		<chatSuitMain ref="chatSuitMain" :username="username" :chatType="chatType" @inputFocused="closeAllModal"
			@openEmoji="openEmoji" @openRecordModal="toggleRecordModal" @openFunModal="openFunModal"></chatSuitMain>
		<chatSuitImage ref="chatSuitImage" :username="username" :chatType="chatType"></chatSuitImage>
		<chat-suit-location id="chat-suit-location" :username="username"></chat-suit-location>
		<chat-suit-video ref="chatSuitVideo" :username="username"></chat-suit-video>
		<chatSuitPtopcall ref="chatSuitPtopcall" :chatType="chatType" @makeVideoCall="onMakeVideoCall">
		</chatSuitPtopcall>

		<view :class="showFunModal">
			<view :class="'other_func ' + (isIPX ? 'other_func_X' : '')" class="flex">
				<view class="send_image" @tap="sendImage">
					<image src="/static/image/chat-imgs-icon.png"></image>
					图片
				</view>
				<view class="send_image" @tap="sendImage">
					<image src="/static/image/chat-gift-icon.png"></image>
					礼物
				</view>
				<view class="send_image" @tap="voiceCallAction">
					<image src="/static/image/chat-audio-icon.png"></image>
					语音
				</view>
				<view class="send_image" @tap="videoCallAction">
					<image src="/static/image/chat-video-icon.png"></image>
					视频
				</view>
				<view class="send_image" @tap="sendLocation">
					<image src="/static/image/chat-place-icon.png"></image>
					发送位置
				</view>
				<navigator hover-class="none" :url="'/pages/index/evaluate?targetUserId='+username.your"
					class="send_image">
					<image src="/static/image/chat-eva-icon.png" mode=""></image>
					评价
				</navigator>
				<!-- <view class="v-record" @tap="callVideo" v-if="username.groupId">
						<image src="/static/images/call2x.png" style="height: 24px; width: 15px" />
					</view> -->
			</view>
		</view>
	</view>
</template>

<script>
	let RecordStatus = require("./suit/audio/record_status").RecordStatus;
	let msgType = require("../msgtype");
	let WebIM = require("@/utils/WebIM")["default"];
	import chatSuitEmoji from "./suit/emoji/emoji";
	import chatSuitImage from "./suit/image/image";
	import chatSuitLocation from "./suit/location/location";
	import chatSuitMain from "./suit/main/main";
	import chatSuitPtopcall from "./suit/ptopcall/ptopcall.vue";
	import permision from "@/js_sdk/wa-permission/permission.js";
	import chatSuitVideo from "./suit/videoComp/videoComp"

	let FUNMODAL_STATUS = {
		OPENED: "showFunModal",
		CLOSED: "fun_list",
	};

	export default {
		data() {
			return {
				recordStatus: RecordStatus.HIDE,
				RecordStatus,
				__comps__: {
					main: null,
					emoji: null,
					image: null,
					location: null,
					video: null,
				},
				isIPX: "",
				showFunModal: FUNMODAL_STATUS.CLOSED,
			};
		},

		components: {
			chatSuitEmoji,
			chatSuitImage,
			chatSuitLocation,
			chatSuitMain,
			chatSuitPtopcall,
			chatSuitVideo
		},
		props: {
			username: {
				type: Object,
				default: () => ({}),
			},
			chatType: {
				type: String,
				default: msgType.chatType.SINGLE_CHAT,
			},
		},

		// lifetimes
		created() {},

		beforeMount() {},

		moved() {},

		destroyed() {},

		onLoad() {
			console.log(1111111111)
			this.setData({
				isIPX: false, //getApp().globalData.isIPX
			});
			// let comps = this.$data.__comps__;
			// comps.main = this.selectComponent("#chatSuitMain");
			// comps.emoji = this.selectComponent("#chatSuitEmoji");
			// comps.image = this.selectComponent("#chatSuitImage");
		},

		methods: {
			getPermission() {
				const res = uni.getSystemInfoSync();
				console.log(res)
				if (res.platform == "android") {
					var result1 = permision.requestAndroidPermission('android.permission.RECORD_AUDIO');
					var result2 = permision.requestAndroidPermission('android.permission.CAMERA');
				} else {
					var result1 = permision.judgeIosPermission("camera");
					var result2 = permision.judgeIosPermission("record");
				}
			},
			voiceCallAction() {
				console.log(this.username)
				// {
				// 	"myName": "jiaoyou5",
				// 	"my_nick": "http://4n4r885407.qicp.vip/profile/upload/2021/12/20/2fac1115-7d90-4a3d-9be3-1258549a174a.png",
				// 	"my_name": "http://4n4r885407.qicp.vip/profile/upload/2021/12/20/2fac1115-7d90-4a3d-9be3-1258549a174a.png",
				// 	"your": "jiaoyou6",
				// 	"your_nick": "http://4n4r885407.qicp.vip/profile/upload/2021/12/27/13b122ed-e716-4ded-b137-39027d28fee1.jpeg",
				// 	"your_name": "嘎嘎"
				// }
				let time=new Date().getTime();
				uni.navigateTo({
					url: '/pages/chat/voiceCall?username=' + JSON.stringify({
						groupId: time,
						name:this.username.your_name,
						nick:this.username.your_nick,
						your: this.username.your,//接听者
						myName: this.username.myName,//拨打者
						isSelf:true,//myName 拨打
					})
				});
				this.inviteVideoAction(1,time);
				this.closeFunModal();
			},
			videoCallAction() {
				let time = new Date().getTime();
				uni.navigateTo({
					url: '/pages/chat/videoCall?username=' + JSON.stringify({
						groupId: time,
						name:this.username.your_name,
						nick:this.username.your_nick,
						your: this.username.your,
						myName: this.username.myName,
						isSelf: true
					})
				});
				this.inviteVideoAction(2, time);
				this.closeFunModal();
			},
			// 进入邀请视频的方法
			inviteVideoAction(extype, time) {
				console.log("inviteVideoAction");
				console.log(this.username);
				let that = this;
				let uid = WebIM.conn.getUniqueId();
				let msg = new WebIM.message("custom", uid);
				msg.set({
					from: that.username.myName,
					to: that.username.your,
					ext: {
						'extmsg': 'inviteVideoAction',
						'extype': extype, //1语音通话,2视频通话
						'inviteCode': 1,
						'name':that.username.my_name,
						'nick':that.username.my_nick,
						'groupId': time
					},
					chatType: that.chatType,
					success(id, serverMsgId) {
						console.log("发送邀请消息成功");
						console.log(id, serverMsgId);
						// disp.fire("em.chat.sendSuccess", id);
					},
					fail(id, serverMsgId) {
						console.log("发送邀请消息失败了", id);
					},
				});
				WebIM.conn.send(msg.body);
			},
			// 事件有长度限制：仅限 26 字符
			toggleRecordModal() {
				this.$emit("tapSendAudio", null, {
					bubbles: true,
					composed: true,
				});
			},

			sendVideo() {
				this.$refs.chatSuitVideo.sendVideo();
			},
			openCamera() {
				this.$refs.chatSuitImage.openCamera();
			},

			openEmoji() {
				setTimeout(() => {
					this.setData({
						showFunModal: FUNMODAL_STATUS.CLOSED,
					});
				}, 100);
				this.$refs.chatSuitEmoji.openEmoji();
			},

			cancelEmoji() {
				this.$refs.chatSuitEmoji.cancelEmoji();
			},

			sendImage() {
				this.$refs.chatSuitImage.sendImage();
			},

			sendLocation() {
				this.data.__comps__.location.sendLocation();
			},

			emojiAction(evt) {
				this.$refs.chatSuitMain.emojiAction(evt.msg);
			},

			callVideo() {
				this.$refs.chatSuitPtopcall.show();
			},

			onMakeVideoCall() {
				console.log("onMakeVideoCall -> inputbar");
				this.$emit("makeVideoCall", null, "single");
			},

			openFunModal() {
				this.setData({
					showFunModal: FUNMODAL_STATUS.OPENED,
				});
				this.cancelEmoji()
			},
			closeFunModal() {
				this.setData({
					showFunModal: FUNMODAL_STATUS.CLOSED,
				});
				this.cancelEmoji()
			},
			closeAllModal() {
				this.cancelEmoji()
				this.closeFunModal()
			},
			edit_group() {
				var nameList = {
					myName: this.username.myName,
					groupName: this.username.your,
					roomId: this.username.groupId,
				};
				uni.navigateTo({
					url: "../groupSetting/groupSetting?groupInfo=" + JSON.stringify(nameList),
				});
			},
		},
	};
</script>
<style>
	@import "./inputbar.css";
</style>
